@import '../partials/mixins';
@import '../partials/variables';

$checkbox-width: 30px;

form {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;

  .input-wrapper, .btn {
    margin: 0 auto;
  }

  @include size(tablet) {
    grid-template-columns: repeat(2, 1fr);
    max-width: 910px;
    margin-left: auto;
    margin-right: auto;

    .input-wrapper, .btn {
      margin-top: 0;
      margin-bottom: 0;
    }

    .checkbox {
      margin-top: 14px;
    }

    .btn {
      grid-column: 1 / span 2;
      margin-top: 56px;
    }
  }
}

input, select, .select-wrapper {
  display: block;
  width: 100%;
}

input, select {
  padding: 0.875em 1em;
  border: 1px solid $grey;

  &.has-error:not(:focus) {
    border: 1px solid $error-color;
    background-color: lighten($error-color, 20);
  }
}

// Some of these get overridden by autofill styles
input, select, button {
  &.disabled {
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: $lightgrey;
    border: 1px solid $lightgrey;
    background-color: lighten($lightgrey, 20);
    cursor: not-allowed;
  }
}

select {
  -webkit-appearance: none;
}

.input-wrapper {
  display: block;
  position: relative;
  width: 100%;

  label {
    display: block;
    margin-bottom: 4px;
    font-size: 0.625em;
  }

  @include size(tablet) {
    max-width: 327px;
  }
}

.select-wrapper {
  position: relative;
  display: inline-block;

  &:before, &:after {
    content: '';
    display: block;
    position: absolute;
    background-color: $grey;
    height: 2px;
    width: 7px;
    top: 55%;
    pointer-events: none;
  }
  &:before {
    right: 25px;
    transform-origin: bottom right;
    transform: rotate(45deg);
  }
  &:after {
    right: 18px;
    transform-origin: bottom left;
    transform: rotate(-45deg);
  }

  select {
    padding-right: 2.5em;
  }

  &.disabled {
    &:before, &:after {
      background-color: $lightgrey;
    }
  }
}

.checkbox {
  padding: 6px 0;
  line-height: $checkbox-width;

  input[type="checkbox"] {
    position: absolute;
    left: -9999px;
    width: 1px;
    height: 1px;
    opacity: 0;
  }

  label {
    position: relative;
    display: inline-block;
    margin: 0;
    padding-left: $checkbox-width + 12px;
    font-size: 1rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:before, &:after {
      position: absolute;
      content: "";
      
      /*Needed for the line-height to take effect*/
      display: inline-block;
    }

    &:before {
      height: $checkbox-width;
      width: $checkbox-width;
      
      border: 1px solid $grey;
      left: 0;
      
      /*(24px line-height - 16px height of fake checkbox) / 2 - 1px for the border
       *to vertically center it.
       */
      top: 0;
    }

    &:after {
      top: 5px;
      left: 4px;
      height: 12px;
      width: 22px;
      border-left: 3px solid $blue;
      border-bottom: 3px solid $blue;
      transform: rotate(300deg) skew(-25deg, 0deg);
    }
  }

  input[type="checkbox"] {
    + label:after {
      content: none;
    }
    &:checked + label:after {
      content: '';
    }
    &:not(.disabled):focus + label:before {
      outline: rgb(59, 153, 252) auto 5px;
    }
    &.disabled + label {
      color: $lightgrey;

      &:before {
        border: 1px solid $lightgrey;
        background-color: lighten($lightgrey, 20);
      }
    }
  }
}

.input-error-msg {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  width: 100%;
  padding: 4px 8px;
  color: white;
  background-color: $error-color;
  z-index: 2;

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 100%;
    left: 12px;
    height: 0;
    width: 0;
    border-color: transparent transparent $error-color transparent;
    border-width: 0 12px 8px 12px;
    border-style: solid;
  }
}
