@import 'partials/mixins';
@import 'partials/variables';

$header-footer-height: 70px;

@keyframes cycle-rotate {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

html, body {
  padding: 0;
  margin: 0;
}

body {
  font-family: Arial, sans-serif;
  font-size: 1em;
  color: $grey;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  margin: 0;
  font-size: 1.625em;
  text-align: center;
  font-weight: 400;

  @include size(tablet) {
    font-size: 2em;
  }
  @include size(desktop) {
    font-size: 2.5em;
  }
}

a {
  color: $blue;
  text-decoration: none;
  transition: all 0.25s ease;
}

input, select, button {
  padding: 0;
  border: none;
  border-radius: 0;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  font: inherit;
  color: inherit;
}

header, footer {
  height: $header-footer-height;
  background-color: $lightblue;
  color: white;
}

footer {
  text-align: center;

  .vertical-center {
    justify-content: center;
  }

  @include size(mobile-max) {
    font-size: 0.625em;
  }
}

hr {
  border: 0 none;
  border-bottom: 1px solid $lightgrey;
}

.outer-wrap {
  position: relative;
  min-height: 100vh;
  padding-bottom: $header-footer-height;

  footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}

.section {
  margin-top: 40px;
  margin-bottom: 40px;

  @include size(tablet) {
    margin-top: 50px;
    margin-bottom: 50px;
  }
  @include size(desktop) {
    margin-top: 75px;
    margin-bottom: 75px;
  }
}

.container {
  clear: both;
  margin: 0 auto;
  max-width: 1300px;
  padding: 0 20px;
  position: relative;
}

.vertical-center {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}

.message {
  font-size: 1.125em;
  text-align: center;
}

.logo {
  display: block;
  width: 71px;
  height: 22px;
  background-size: 100% 100%;
  background-size: contain;
  background-position: left center;
  background-repeat: no-repeat;
}

.btn {
  position: relative;
  height: 40px;
  padding: 0 36px;
  background-color: $blue;
  border-radius: 3px;
  font-size: 0.625em;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;

  &.loading {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-radius: 3px;
      background-color: rgba(0,0,0,0.6);
    }

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      height: 20px;
      width: 20px;
      border-radius: 10px;
      border: 3px solid;
      border-color: white white white transparent;
      transform-origin: center center;
      animation: cycle-rotate 1s infinite linear;
    }
  }
}

.badge {
  padding: 4px 18px;
  background-color: $blue;
  color: white;
  font-size: 1rem;
}
