@import '../partials/mixins';
@import '../partials/variables';

.card-group {
  margin: 36px 0;
}

.card {
  margin: 16px 0;
  border: 1px solid $lightgrey;
}

.card-section {
  margin: 0;
  padding: 13px 13px;
  font-size: 0.875em;
  
  // All but first child
  &:nth-child(n+2) {
    border-top: 1px solid $lightgrey;
  }

  .title {
    margin: 3px 0 1em;
    font-size: 1rem;

    @include size(mobile-max) {
      a {
        // This seems to be the only way to get word-wrap to work next to the floated
        // badges on mobile Safari, but creates some less-than-ideal click regions.
        // Seems better than forcing the <a> below the badge. Other alternative is
        // dynamically restricting the width of the .title element, but requires JS.
        display: block;
      }
    }
  }

  .title, .description {
    // Prevents overflow of very long strings - looks better visually but potentially more confusing
    word-wrap: break-word;
    overflow-wrap: break-word;
    word-break: break-word;
  }

  .badge {
    float: right;
    margin-left: 16px;
    // To float on top of full-width `.title a` element
    z-index: 10;
  }

  p {
    margin: 4px 0;
  }
}

@include size(tablet) {
  .card-group {
    margin: 44px 0;
  }
  .card-section {
    .title {
      font-size: 1.125rem;
    }
  }
}

@include size(desktop) {
  .card {
    display: grid;
    // This won't work if there aren't exactly two .card-section elements
    grid-template-columns: 1fr 148px 148px;
  }

  .card-section {
    padding: 18px 18px;

    &:nth-child(n+2) {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      border-top: 0;
      border-left: 1px solid $lightgrey;
      text-align: center;

      strong {
        display: block;
        margin-top: 0.75em;
      }
    }

    .title {
      margin-top: 0;
    }
  }
}